export default {
	data() {
		return {
			dynamic1: true,
			dynamic2: false,
			dynamic3: false,
			typeA: true,
			typeB: true,
			ImgUrl: this.ImgUrl
		}
	},
	methods: {
		//切换内容
		tapInformation(e) {
			if (e == 1) {
				this.dynamic1 = true;
				this.dynamic2 = false;
				this.dynamic3 = false;
				this.typeA = true;
				this.typeB = true;
			} else if (e == 2) {
				this.dynamic1 = false;
				this.dynamic2 = true;
				this.dynamic3 = false;
				this.typeA = true;
				this.typeB = false;
			}
			else if (e == 3) {
				this.dynamic1 = false;
				this.dynamic2 = false;
				this.dynamic3 = true;
				this.typeA = false;
				this.typeB = true;
			}
		},
		
		// 获取活动
		getUserAct() {
			let baseUrl = this.WebUrl
			let that = this
			let status = -1
			if(!this.typeA) {
				status = 1
			}else if (!this.typeB) {
				status = 0
			}
			uni.getStorage({
				key: 'u_id',
				success: (id) => {
					uni.request({
						url: baseUrl + '/getUserAct',
						data: {
							u_id: id.data,
							status: status
						},
						success: (res) => {
							console.log(res)
							res.data.data.forEach(function(item,index){
								begin_date = item.begin_time.split(' ')[0]
								begin_time = item.begin_time.split(' ')[1]
								end_date = item.end_time.split(' ')[0]
								end_time = item.end_time.split(' ')[1]
								let data = {
									title: item.title,
									begin_time: begin_time,
									begin_date: begin_date,
									end_time: end_time,
									end_date: end_date,
									address: item.address,
									join: item.join,
									browse: item.browse,
									status: item.status
								}
							})
						}
					})
				}
			})
		}
	},
	onLoad() {
		this.getUserAct()
	}
}
